import React from "react";
import { TiTick } from "react-icons/ti";
import styled from "styled-components";
import { useAddStudent } from "../../../Context/AddStudent";
import { handleError } from "../../../utils/Toastify";

const AddNew = () => {
  const students = useAddStudent();
  return (
    <div>
      <p style={{ fontWeight: "600", marginBottom: "0", marginTop: "10px" }}>
        Add New
      </p>
      <FlexRow>
        <InputGroup>
          <Label>Student ID:</Label>
          <Input
            id="stuId"
            className="stuId"
            type="Number"
            defaultValue={""}
            placeholder="18052801001"
            onChange={(e) => {}}
          />
        </InputGroup>
        <InputGroup>
          <Label>Student Name:</Label>
          <Input
            id="stuName"
            name="name"
            type="text"
            defaultValue={""}
            placeholder="Mr. John"
            onChange={(e) => {}}
          />
        </InputGroup>
        <ButtonGroup>
          <IconButton
            style={{ right: "0", background: "green" }}
            onClick={() => {
              const stuId = document.getElementById("stuId").value;
              console.log(Object.values(students.array));
              console.log(Object.values(students.array).toString());
              const flag = Object.values(students.array)
                .toString()
                .includes(stuId);
              const stuName = document.getElementById("stuName").value;
              if (flag) {
                handleError(`${stuId} is already in the list`);
              }
              if (stuId !== "" && stuName !== "" && !flag) {
                // setLoading(true);
                const { year, semester, term, classId, courseName } =
                  students.classInfo;
                students.setArray([
                  ...students.array,
                  [
                    Number(stuId),
                    `${stuName}`,
                    classId,
                    year,
                    semester,
                    `${courseName}`,
                    term,
                    new Date().toUTCString(),
                  ],
                ]);
                document.getElementById("stuId").value = Number(stuId) + 1;
                document.getElementById("stuName").value = "";
              }
            }}
          >
            <TiTick />
          </IconButton>
        </ButtonGroup>
        {/* {handleSubmitImportfromOther} */}
      </FlexRow>
    </div>
  );
};

export default AddNew;

const ButtonGroup = styled.div`
  display: flex;
  align-items: end;
  justify-content: flex-start;
  padding-top: 5px;
  padding-bottom: 1rem;

  @media (max-width: 560px) {
    width: 100%;
    justify-content: flex-end;
    align-items: flex-start;
    padding-top: 0;
  }
`;

const Label = styled.label`
  font-size: 0.9rem;
`;

const Input = styled.input`
  padding: 0.4rem 0.5rem;
  border-radius: 3px;
  margin-bottom: 0.3rem;
  border: 1px solid rgba(3, 3, 3, 0.5);

  &::placeholder {
    color: rgba(3, 3, 3, 0.5);
  }
`;
const IconButton = styled.div`
  width: 30px;
  height: 30px;
  border-radius: 50x;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50px;
  color: #fff;
  font-size: 1.2rem;
  cursor: pointer;
  &:nth-child(1) {
    margin: 0 5px;
  }
`;

const InputGroup = styled.div`
  display: flex;
  flex-direction: column;
  margin-bottom: 0.5rem;
  &:nth-child(1) {
    margin-right: 5px;
  }
`;
const FlexRow = styled.div`
  display: grid;
  grid-template-columns: auto auto 50px;
  overflow: hidden;
  width: 100%;

  @media (max-width: 560px) {
    grid-template-columns: auto;
  }
`;
